	module.exports = {"data":{"markdownRemark":{"html":"<p>これ1つでステージ/敵(ボス/雑魚/使い魔)/弾/イベントスクリプトと幅広くカバーできる中～大規模スクリプト向け東方弾幕風フレームワークライブラリ『<a href=\"/tag/flan\">Framework of LunAtic Notation</a>』を作ってみましたので公開してみるテスト。いやまぁ小規模スクリプトでも使えないわけじゃないですが、ちょっとボリューム的に荷が重いかもってことで。まだろくすっぽテストしてないので人柱キボンヌ。 本当に夏コミ間に合うのか不安になってきた 'Ａ｀ そんな荒んだ心の癒しとなるのが <a href=\"/harbot\">ハーボット</a>。名前はまだない。</p>\n<h2>2006/7/22追記</h2>\n<p>東方弾幕風フレームワークライブラリ<a href=\"/tag/flan\">Framework of LunAtic Notation</a> version0.0dをリリースしました。サンプル追加&#x26;エラーが1行じゃ書ききれない程に大量に出たので大量につぶしました。</p>\n<h2>(拍)行け、魔理沙！霊夢を救うのだ！</h2>\n<p>♪そうさ～お前は～～愛に～彷徨い～～歩き続ける～旅人～(それなんか違</p>\n<h2>(拍)＞(それもかなり関係ないネタ)宣伝しに来てた事無かったでしたっけ？</h2>\n<p>ククク……どこに鼠……自作自演の輩が潜んでおるか知れんからな……っ</p>\n<h2>(拍)でも発動条件ちょっときつすぎる気がしました。</h2>\n<p>半分ネタバレ防止の意味も含めて厳しめにしたんですが、もうちょっとゆるくするよう検討します。</p>\n<h2>(拍)メロンパーン！！！！！！</h2>\n<p>しかしつい去年まで食わず嫌いだったりしてた 'Ａ｀</p>\n<h2>(拍)ちーちんかあいい??????????????????！！</h2>\n<p>うぉぉなんか文字化けてる！スクリプトのミスかな？折角書いてくれたのにｽﾏﾝとです。</p>\n<h2>(拍)マーティンカードとどう効果が違うんですか？</h2>\n<p>暗闇耐性と鳥目耐性は属性が違うんですよきっと。</p>\n<h2>(拍)はぅーーーーお持ち帰りしていいかな？かな？</h2>\n<p>いいけど料理しないでね、とりあえず怖がるからその鉈は隠したほうがいいと思うお(^ω^)</p>\n<h2>(拍)この、ひよっこがｗｗｗ</h2>\n<p>やっつけるとピヨピヨピヨ…って泣いて倒れます。</p>","frontmatter":{"cover":null,"date":"2006-07-21T00:00:00+09:00","strDate":"2006/7/20","title":"弾幕風ライブラリ作ってみました"}}},"pathContext":{"path":"/ja/blog/2006/07/21-弾幕風ライブラリ作ってみました/","langKey":"ja"}}

